import { Component, OnInit } from '@angular/core';
import { trigger, style, animate, transition } from '@angular/animations';

import { PopupService } from './../../core/services/popup.service';
import { ModalService } from './../../core/services/modal.service';
import { LangService } from './../../core/services/references/lang.service';
import { Router } from '@angular/router';
import { UserService } from '@services/user.service';
import { UserConfigService } from '@app/modules/core/services/references/user-configuration.service';

@Component({
  selector: 'itx-helpdesk-new',
  templateUrl: './helpdesk-new.component.html',
  styleUrls: ['./helpdesk-new.component.scss'],
  animations: [
    trigger('blockState', [
      transition('void => *', [
        style({ bottom: '-100%' }),
        animate('250ms ease-out', style({ bottom: '0' }))
      ]),
      transition(':leave', [
        style({ bottom: '0' }),
        animate('250ms ease-in', style({ bottom: '-100%' }))
      ]),
    ])
  ]
})
export class HelpdeskNewComponent implements OnInit {

  public translation;
  public userInfos;
  public createNew: boolean;
  public showSubMenuBool: boolean;
  public userConfig: any;

  constructor(
    private popupService: PopupService,
    private modalService: ModalService,
    private langService: LangService,
    private router: Router,
    private userService: UserService,
    private userConfigService: UserConfigService
  ) {
    this.showSubMenuBool = false;
    this.translation = this.langService.lang;
    this.userConfig = this.userConfigService.userconfig;
    this.modalService.modalState$.subscribe(res => {
      this.createNew = res;
    });

    this.userService.userInfos$.subscribe((userInfos) => {
      this.userInfos = userInfos;
    });
  }

  ngOnInit(): void {
    const popupState: boolean = this.popupService.popupState;
    if (popupState) {
      this.popupService.setPopupState(false);
    }
    this.userService.getUserInfos().subscribe();
  }

  public navigateTo(route: string): void {
    this.router.navigate([{ outlets: { modal: null } }], { skipLocationChange: true, queryParams: { skipLocationChange: true } }).then(res => {
      this.router.navigate([route]);
    });
    // this.router.navigate([route]);
  }

  public showSubMenu(): void {
    this.showSubMenuBool = !this.showSubMenuBool;
  }

}
